@use "global" as *;


// 『投稿詳細ページ』内の「次の投稿へボタン」と「前の投稿へボタン」
.pagenavi {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

// 「次へ」「前へ」リンクのスタイル
.pagenavi__next,
.pagenavi__prev {  // 『.pagenavi__next』と『.pagenavi__prev』の共通の指定
  aspect-ratio: 1 / 1;
  height: rem(38);  // ページネーションは高さが絶対に変わらないから、高さを指定してOK。
  min-width: rem(38);  // 最小幅を指定。
  padding-inline: rem(3);  // paddingは、横方向のみ最低限を指定。縦方向の指定は不要。
  display: grid;
  place-items: center;

  border: rem(1) solid $color-accent1;
  background: $color-background-base;
  transition: background 0.2s ease-out;

  position: relative;
}

.pagenavi__prev {
  margin-right: rem(28);
}

.pagenavi__prev::before {  // 『前の投稿へのボタン』の中身の矢印を擬似要素で作成 (『＜』)
  content: "";
  width: rem(10);
  height: rem(10);
  border-left: solid rem(1) $color-accent1;
  border-top: solid rem(1) $color-accent1;
  position: absolute;
  top: 50%;
  left: calc(50% + rem(3));  // 真ん中に寄せるために位置調整。
  transform: translate(-50%, -50%) rotate(-45deg);  // 回転させて矢印に。

  transition: border 0.2s ease-out;
}

.pagenavi__next {
  margin-left: rem(28);
}

.pagenavi__next::before {  // 『次の投稿へのボタン』の中身の矢印を擬似要素で作成 (『＞』)
  content: "";
  width: rem(10);
  height: rem(10);
  border-right: solid rem(1) $color-accent1;
  border-top: solid rem(1) $color-accent1;
  position: absolute;
  top: 50%;
  left: calc(50% - rem(3));  // 真ん中に寄せるために位置調整。
  transform: translate(-50%, -50%) rotate(45deg);  // 回転させて矢印に。

  transition: border 0.2s ease-out;
}

.pagenavi__next:hover,
.pagenavi__prev:hover {  // hover
  background: $color-accent1;
}

.pagenavi__prev:hover:before {  // hover
  border-left: solid rem(1) #FFF;
  border-top: solid rem(1) #FFF;
}

.pagenavi__next:hover:before {  // hover
  border-right: solid rem(1) #FFF;
  border-top: solid rem(1) #FFF;
}
