@use "global" as *;


.cta {
  position: relative;
}

.cta__wrap {

  @include mq(md) {
    position: relative;
  }
}

.cta__wrap::after {  // 魚の群れ(PC時)
  @include mq(md) {
    content: "";
    background: url(../images/common/fishes.png) no-repeat center center / contain;
    width: rem(303);
    height: rem(144);
    position: absolute;
    top: rem(-40);
    right: calc(50% - min(48vw, 41.7rem));  // ※位置を制御
  }
}

.cta__box {
  background: #FFF;
  margin-inline: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(36);

  @include mq(md) {
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.25);
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding-top: rem(80);
    padding-bottom: rem(75);
    padding-left: rem(50);
    padding-right: rem(113);
  }
}

.cta__top {
  max-width: 100%;
  width: 100%;
  text-align: center;

  @include mq(md) {
    flex-shrink: 0;
    width: auto;
  }
}

.cta__head {
  position: relative;
}

.cta__head::after {  // 緑の仕切り線
  content: "";
  max-width: rem(500);  // 広がり過ぎないように、よしなに調整
  width: 100%;
  height: 1px;
  background: $color-accent1;
  position: absolute;
  bottom: rem(-6);
  left: 50%;
  transform: translateX(-50%);

  @include mq(md) {
    max-width: 100%;
    bottom: rem(-8);
  }
}

.cta__logo {
  width: fit-content;
  margin-inline: auto;
  position: relative;
}

.cta__logo::after {  // 魚の群れ(SP時)
  content: "";
  width: rem(112);
  height: rem(54);
  background: url(../images/common/fishes.png) no-repeat center center / contain;
  position: absolute;
  top: rem(-26);
  right: rem(-85);

  @include mq(md) {
    display: none;
  }
}

.cta__logo img {
  max-width: rem(174);
  width: 100%;
  margin-inline: auto;

  @include mq(md) {
    max-width: rem(200);
  }
}

.cta__info {
  margin-top: rem(22);

  @include mq(md) {
    margin-top: rem(32);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: rem(21);
  }
}

.cta__texts {
  color: $color-font-base;
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(4);

  @include mq(md) {
    flex-shrink: 0;
    align-items: flex-start;
  }
}

.cta__address {
  font-style: normal;
}

.cta__tel {
}

.cta_hours {
}

.cta__holiday {
}

.cta__map {
  margin-top: rem(15);
  max-width: rem(500);
  width: calc(295px / 375px * 100vw);
  height: calc(160px / 375px * 100vw);
  aspect-ratio: 295 / 160;
  margin-inline: auto;

  @include mq(md) {
    width: calc(273px / 1440px * 100vw);
    max-width: rem(237);
    // min-width: rem(160);
    height: calc(148px / 1440px * 100vw);
    max-height: rem(148);
    // min-height: rem(110);
    aspect-ratio: 273 / 148;
    margin-top: 0;
  }
}

.cta__map iframe {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);

  @include mq(md) {
    box-shadow: none;
  }
}

.cta__divide {  // 緑の縦の仕切り線(PC時のみ表示)
  display: none;

  @include mq(md) {
    display: block;
    content: "";
    width: 1px;
    height: rem(255);
    background: $color-accent1;
  }
}

