@use "global" as *;


.section-head {
  text-align: center;
}

.section-head__title {
  display: block;
  color: $color-font-base;
  font-family: $font-family-en-1;
  font-size: rem(32);
  font-weight: $normal;
  line-height: 1.25;

  @include mq(md) {
    font-size: rem(40);
    line-height: 1;
  }
}

.section-head__title::first-letter {  // 先頭の文字だけを指定
  color: $color-accent1;
}

.section-head__subtitle {
  display: block;
  color: $color-accent1;
  font-size: rem(12);
  font-weight: $normal;
  line-height: 1.5;
  margin-top: rem(4);

  @include mq(md) {
    margin-top: rem(8);
  }
}


// Contact特有の指定(モディファイア)
.section-head__title--contact {
  font-size: rem(80);
  line-height: 1;

  @include mq(md) {
    font-size: rem(80);
    line-height: 1;
  }
}

.section-head__subtitle--contact {
  margin-top: 0;
  transform: translateY(-8px);

  @include mq(md) {
    margin-top: 0;
    transform: translateY(-7px);
  }
}


// 白いタイトル(PC時のみ) (モディファイア)
// 先頭以外の文字 (白)
.section-head__title--white {
  @include mq(md) {
    color: #FFF;
  }
}

.section-head__subtitle--white {
  @include mq(md) {
    color: #FFF;
  }
}


// 先頭の文字 (薄い青色)
.section-head__title--white::first-letter {
  @include mq(md) {
    color: #DDF0F1;
  }
}