@use "global" as *;


.privacy {

}

.privacy__inner {
  max-width: rem(500);
  position: relative;

  @include mq(md) {
    max-width: rem(calc(800 + 25 * 2));  // 両サイドのpaddingを考慮
  }
}

.privacy__inner::before {  // 魚の群れ
  content: "";
  background: url(../images/common/fishes.png) no-repeat center center / contain;
  width: rem(120);
  max-width: rem(120);
  height: rem(55);
  max-height: rem(55);
  transform: scaleX(-1);  // 左右反転。
  position: absolute;
  top: rem(-24);
  left: calc(50% - min(48vw, 39rem));  // *位置を制御
  z-index: -1;

  @include mq(md) {
    width: rem(308);
    max-width: rem(308);
    height: rem(145);
    max-height: rem(145);
    top: rem(-70);
    left: calc(50% - min(48vw, 38rem));  // *位置を制御
  }
}

.privacy__title {
  color: $color-font-base;
  font-size: rem(24);
  font-weight: 500;
  line-height: normal;

  @include mq(md) {
    font-size: rem(32);
  }
}

.privacy__contents-wrap {
  margin-top: rem(40);
  color: $color-font-base;
}




// 管理画面から指定出来るようにするために、『<?php the_content(); ?>』で指定するため、タグ自体にCSSを指定。
.privacy__contents-wrap p {
  margin-top: rem(32);

  font-size: rem(16);
  font-weight: $normal;
  line-height: 2.0;
}

.privacy__contents-wrap ol {
  margin-top: rem(10);

  list-style-type: decimal;  /* 通常の数字（1, 2, 3...）を表示 */
  padding-left: rem(16);

  font-size: rem(16);
  font-weight: $normal;
  line-height: 2.0;
}

.privacy__contents-wrap ul {
  margin-top: rem(32);

  list-style-type: disc;  /* 「・」を表示 */

  font-size: rem(16);
  font-weight: $normal;
  line-height: 2.0;
}

