@use "global" as *;


// ======================================================================
// プラグイン『Contact Form 7』用のクラスにスタイルを調整 (上書き)
// かつ、『同意』用のチェックボックス用のCSSを指定
// ======================================================================
.agree-checkbox .wpcf7-list-item-label {  // ラベルのテキスト部分に付与されるクラス名
  display: inline-block;
  padding-left: rem(30);
  font-size: rem(14);
  font-weight: $normal;
  line-height: 1.57;
}

.agree-checkbox__text::before {  // チェックボックスの外枠の四角。
  content: "";
  width: rem(22);
  height: rem(22);
  border: 1px solid $color-accent1;
  background: #FFF;
  position: absolute;
  top: 0;
  left: 0;

  @include mq(md) {
    top: rem(2.5);
  }
}

.agree-checkbox__text::after {  // チェックボックスのチェックマークアイコン。
  content: "";
  width: rem(19);
  height: rem(14);
  background: url(../images/common/checkbox_check-icon.svg) no-repeat center center / contain;
  position: absolute;
  top: rem(4);
  left: rem(1.4);
  opacity: 0;

  @include mq(md) {
    top: rem(6.5);
  }
}

.agree-checkbox .wpcf7-list-item-label::before {  // チェックボックスの外枠の四角
  content: "";
  width: rem(22);
  height: rem(22);
  border: 1px solid $color-accent1;
  background: #FFF;
  position: absolute;
  top: rem(10) !important;
  left: 0;

  @include mq(md) {
    top: rem(2.5);
  }
}

.agree-checkbox .wpcf7-list-item-label::after {  // チェックボックスのチェックマークアイコン。
  content: "";
  width: rem(19);
  height: rem(14);
  background: url(../images/common/checkbox_check-icon.svg) no-repeat center center / contain;
  position: absolute;
  top: rem(11) !important;
  left: rem(1.4);
  opacity: 0;

  @include mq(md) {
    top: rem(6.5);
  }
}
