@use "global" as *;


/* ブロック要素（パンくず全体） */
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  color: $color-font-base;
  font-size: rem(14);
}

.breadcrumb span {  // プラグイン『Breadcrumb NavXT』でデフォルトで生成される要素に指定。
  margin-right: rem(8);  // パンくずアイテム間のスペース
  background: none;  // デフォルトで、『post』というクラス名が付いてしまうため、他の部分で使っている『.post』のCSSを打ち消すようために上書き。
  box-shadow: none;  // デフォルトで、『post』というクラス名が付いてしまうため、他の部分で使っている『.post』のCSSを打ち消すようために上書き。
}

.breadcrumb span:last-child {
  margin-right: 0;  // 最後のアイテムにはスペースを適用しない
}

.breadcrumb span a {
  transition: all 0.2s ease-out;
  background: none;  // デフォルトで、『post』というクラス名が付いてしまうため、他の部分で使っている『.post』のCSSを打ち消すようために上書き。
  box-shadow: none;  // デフォルトで、『post』というクラス名が付いてしまうため、他の部分で使っている『.post』のCSSを打ち消すようために上書き。
}

.breadcrumb span a:hover {  // hover
  color: $color-accent1;
  text-decoration: underline; /* ホバー時に下線を表示 */
}

.breadcrumb span span {
  margin-right: 0;
}

.breadcrumb__separator {  // プラグイン『Breadcrumb NavXT』を、管理画面から設定する際に、『一般』→『パンくずの区切り』の部分に、『<span class="breadcrumb__separator"></span>』を指定して、このHTML形式で生成されるように指定。
  width: rem(6);
  position: relative;
}

.breadcrumb__separator::after {  // 『>』マークを擬似要素で作成
  content: "";
  width: rem(6);
  height: rem(6);
  border-top: 1px solid $color-font-base;
  border-right: 1px solid $color-font-base;
  position: absolute;
  top: 50%;
  left: calc(50% - rem(1));
  transform: translate(-50%, -50%) rotate(45deg);
}




// =======================================
// モディファイア
// =======================================
// .breadcrumbs--white (404ページの、白色のパンくずリスト)
.breadcrumb--white {
  color: #FFF;
}

.breadcrumb--white span a:hover {  // hover
  color: #000;
}

.breadcrumb--white .breadcrumb__separator::after {  // 『>』マークを擬似要素で作成
  content: "";
  width: rem(6);
  height: rem(6);
  border-top: 1px solid #FFF;
  border-right: 1px solid #FFF;
  position: absolute;
  top: 50%;
  left: calc(50% - rem(1));
  transform: translate(-50%, -50%) rotate(45deg);
}
