/* ブロック要素（パンくず全体） */
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  color: #0D2936;
  font-size: 0.875rem;
}

.breadcrumb span {
  margin-right: 0.5rem;
  background: none;
  box-shadow: none;
}

.breadcrumb span:last-child {
  margin-right: 0;
}

.breadcrumb span a {
  transition: all 0.2s ease-out;
  background: none;
  box-shadow: none;
}

.breadcrumb span a:hover {
  color: #408F95;
  text-decoration: underline; /* ホバー時に下線を表示 */
}

.breadcrumb span span {
  margin-right: 0;
}

.breadcrumb__separator {
  width: 0.375rem;
  position: relative;
}

.breadcrumb__separator::after {
  content: "";
  width: 0.375rem;
  height: 0.375rem;
  border-top: 1px solid #0D2936;
  border-right: 1px solid #0D2936;
  position: absolute;
  top: 50%;
  left: calc(50% - 0.0625rem);
  transform: translate(-50%, -50%) rotate(45deg);
}

.breadcrumb--white {
  color: #FFF;
}

.breadcrumb--white span a:hover {
  color: #000;
}

.breadcrumb--white .breadcrumb__separator::after {
  content: "";
  width: 0.375rem;
  height: 0.375rem;
  border-top: 1px solid #FFF;
  border-right: 1px solid #FFF;
  position: absolute;
  top: 50%;
  left: calc(50% - 0.0625rem);
  transform: translate(-50%, -50%) rotate(45deg);
}

