.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding-inline: 2.5rem;
  padding-block: 1rem;
  border: 0.0625rem solid #408F95;
  color: #FFF;
  background: #408F95;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.57;
  position: relative;
  z-index: 1;
  transition: color 0.1s ease-out;
}

.btn:hover {
  color: #408F95;
}

.btn:hover .btn__arrow svg path {
  stroke: #408F95;
}

.btn:hover::after {
  transform: scaleX(1);
  transition: transform 0.2s cubic-bezier(0.45, 0, 0.55, 1);
}

.btn::before {
  content: "";
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  z-index: -1;
}

.btn::after {
  content: "";
  width: 100%;
  height: 100%;
  background: #FFF;
  position: absolute;
  top: 0;
  left: 0;
  transform: scaleX(0);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.btn__arrow {
  display: grid;
  place-items: center;
  margin-top: -0.375rem;
}

.btn__arrow svg path {
  transition: stroke 0.1s ease-out;
}

.btn--white.btn {
  border: 0.0625rem solid #FFF;
  color: #408F95;
  background: #FFF;
}

.btn--white.btn:hover {
  color: #FFF;
}

.btn--white.btn:hover .btn__arrow svg path {
  stroke: #FFF;
}

.btn--white.btn::before {
  border: 0.0625rem solid #FFF;
}

.btn--white.btn::after {
  background: #408F95;
}

.btn--white .btn__arrow svg path {
  stroke: #408F95;
}

.btn--white .btn__arrow svg path {
  transition: stroke 0.1s ease-out;
}

