.pagenavi__next,
.pagenavi__prev {
  aspect-ratio: 1/1;
  height: 2.375rem;
  min-width: 2.375rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  border: 0.0625rem solid #408F95;
  background: #FFF;
  transition: background 0.2s ease-out;
  position: relative;
}

.pagenavi__prev {
  margin-right: 1.75rem;
}

.pagenavi__prev::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-left: solid 0.0625rem #408F95;
  border-top: solid 0.0625rem #408F95;
  position: absolute;
  top: 50%;
  left: calc(50% + 0.1875rem);
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: border 0.2s ease-out;
}

.pagenavi__next {
  margin-left: 1.75rem;
}

.pagenavi__next::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-right: solid 0.0625rem #408F95;
  border-top: solid 0.0625rem #408F95;
  position: absolute;
  top: 50%;
  left: calc(50% - 0.1875rem);
  transform: translate(-50%, -50%) rotate(45deg);
  transition: border 0.2s ease-out;
}

.pagenavi__next:hover,
.pagenavi__prev:hover {
  background: #408F95;
}

.pagenavi__prev:hover:before {
  border-left: solid 0.0625rem #FFF;
  border-top: solid 0.0625rem #FFF;
}

.pagenavi__next:hover:before {
  border-right: solid 0.0625rem #FFF;
  border-top: solid 0.0625rem #FFF;
}

