/* ページネーション全体のスタイル */
.wp-pagenavi {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
}

/* ページ番号リンクのスタイル */
.wp-pagenavi .page {
  height: 2.375rem;
  min-width: 2.375rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  border: 0.0625rem solid #408F95;
  background: #FFF;
  color: #408F95;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: normal;
  transition: all 0.2s ease-out;
}

.wp-pagenavi .page:hover {
  background: #408F95;
  color: #FFF;
}

/* 現在のページ番号のスタイル */
.wp-pagenavi .current {
  height: 2.375rem;
  min-width: 2.375rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  border: 0.0625rem solid #408F95;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: normal;
  background-color: #408F95;
  color: #FFF;
}

/* 「次へ」「前へ」リンクのスタイル */
.previouspostslink {
  margin-right: 1.75rem;
  position: relative;
}

.previouspostslink::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-left: solid 0.0625rem #408F95;
  border-top: solid 0.0625rem #408F95;
  position: absolute;
  top: 50%;
  left: calc(50% + 0.1875rem);
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: border 0.2s ease-out;
}

.nextpostslink {
  margin-left: 1.75rem;
  position: relative;
}

.nextpostslink::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-right: solid 0.0625rem #408F95;
  border-top: solid 0.0625rem #408F95;
  position: absolute;
  top: 50%;
  left: calc(50% - 0.1875rem);
  transform: translate(-50%, -50%) rotate(45deg);
  transition: border 0.2s ease-out;
}

.wp-pagenavi .nextpostslink,
.wp-pagenavi .previouspostslink {
  height: 2.375rem;
  min-width: 2.375rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  border: 0.0625rem solid #408F95;
  background: #FFF;
  transition: background 0.2s ease-out;
}

.wp-pagenavi .nextpostslink:hover,
.wp-pagenavi .previouspostslink:hover {
  background: #408F95;
}

.wp-pagenavi .previouspostslink:hover:before {
  border-left: solid 0.0625rem #FFF;
  border-top: solid 0.0625rem #FFF;
}

.wp-pagenavi .nextpostslink:hover:before {
  border-right: solid 0.0625rem #FFF;
  border-top: solid 0.0625rem #FFF;
}